export default {
	data() {
		return {
			page:1,
			xiaolei: null,
			fou: '0',
			currt: "3", //1是待付款，2是待发货，3是待收货，4完成
			tabCurrentIndex: 0,
			navList: [{
				"text": '全部',
				"id":-1
			}],
			goodsData:null

		}
	},
	onLoad(option) {
		// 页面显示是默认选中第一个
		var content = option.content;
		this.tabCurrentIndex = 0;
		let params = {};
		let url = "/api/goods/category";
		this.util.request(url, "POST", params, (res) => {
			console.log(JSON.stringify(res));
			if (res.statusCode == 200) {
				if (res.data.code == 1) {
					this.data = res.data.data;
					for (var i = 0; i < this.data.categorydata.length; i++) {
						var neirong = {
							"text": this.data.categorydata[i].name,
							"id":this.data.categorydata[i].id
						}
						this.navList.push(neirong);
					}
				} else {
					this.util.showWindow(res.data.msg);
				}
			} else {
				this.util.showWindow("请求错误");
			}
		});
		//查询全部商品
		let params1 = {
			"page":this.page,
			"name":content
		};
		let url1 = "/api/goods";
		this.util.request(url1, "POST", params1, (res) => {
			console.log(JSON.stringify(res));
			if (res.statusCode == 200) {
				if (res.data.code == 1) {
					this.goodsData = res.data.data;
				} else {
					this.util.showWindow(res.data.msg);
				}
			} else {
				this.util.showWindow("请求错误");
			}
		});
	},
	methods: {
		changeTab(e) {
			this.tabCurrentIndex = e.target.current;
		},
		//顶部tab点击
		tabClick(index) {
			this.tabCurrentIndex = index;
			if (index != 0) {
				this.fou = 1;
				this.xiaolei = this.data.categorydata[index-1].childlist;
			} else {
				this.fou = 0;
				//请求全部商品
				let params1 = {
					"page":this.page
				};
				let url1 = "/api/goods";
				this.util.request(url1, "POST", params1, (res) => {
					console.log(JSON.stringify(res));
					if (res.statusCode == 200) {
						if (res.data.code == 1) {
							this.goodsData = res.data.data;
						} else {
							this.util.showWindow(res.data.msg);
						}
					} else {
						this.util.showWindow("请求错误");
					}
				});
			}
		},
		cancer() {
			this.fou = 0;
		},
		select(id) {
			let params = {
				"id":id,
				"page":this.page
			};
			let url = "/api/goods";
			this.util.request(url, "POST", params, (res) => {
				console.log(JSON.stringify(res));
				if (res.statusCode == 200) {
					if (res.data.code == 1) {
						this.goodsData = res.data.data;
					} else {
						this.util.showWindow(res.data.msg);
					}
				} else {
					this.util.showWindow("请求错误");
				}
			});
		},
		goDetail(id) {
			uni.navigateTo({
				url: "../shop_detail/shop_detail?id="+id
			})
		}
	}
}
